<template>
  <div class="back">
    <div class="change-password-container">
      <h2 class="title">修改密码</h2>
      <form @submit.prevent="handleSubmit" class="form">
        <div class="input-group">
          <label for="currentPassword">当前密码</label>
          <input
            type="password"
            id="currentPassword"
            v-model="currentPassword"
            required
            placeholder="请输入当前密码"
          />
        </div>
        <div class="input-group">
          <label for="newPassword">新密码</label>
          <input
            type="password"
            id="newPassword"
            v-model="newPassword"
            required
            placeholder="请输入新密码"
          />
        </div>
        <div class="input-group">
          <label for="confirmPassword">确认新密码</label>
          <input
            type="password"
            id="confirmPassword"
            v-model="confirmPassword"
            required
            placeholder="请确认新密码"
          />
        </div>
        <button type="submit" class="submit-button">提交</button>
      </form>
      <div v-if="errorMessage" class="error-message">{{ errorMessage }}</div>
      <div v-if="successMessage" class="success-message">{{ successMessage }}</div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      currentPassword: '',
      newPassword: '',
      confirmPassword: '',
      errorMessage: '',
      successMessage: '',
    };
  },
  methods: {
    async handleSubmit() {
      if (this.newPassword !== this.confirmPassword) {
        this.errorMessage = '新密码与确认密码不匹配';
        this.successMessage = '';
        return;
      }

      try { 
        const token = localStorage.getItem('token');   
        const response = await axios.post(
          'http://localhost:8090/update_password',
          {
            username: localStorage.getItem('username'), 
            new_password: this.newPassword,
          },
          {
            headers: {
              Authorization: `Bearer ${token}`, 
            },
          }
        );

        
        if (response.data.message) {
          this.successMessage = response.data.message;
          this.errorMessage = '';
        }
      } catch (error) {
        
        this.errorMessage = error.response?.data?.error || '修改密码失败';
        this.successMessage = '';
      } finally {
       
        this.currentPassword = '';
        this.newPassword = '';
        this.confirmPassword = '';
      }
    },
  },
};
</script>




<style scoped>
.back{
  width: 100vw; /* Full viewport width */
  height: 100vh;
  background: linear-gradient(135deg, #fbc2eb, #a6c0fe);
}
.change-password-container {
  max-width: 400px;
  margin: 50px auto;
  padding: 20px;
  border-radius: 10px;
  background-color: #f9f9f9;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.title {
  font-size: 24px;
  margin-bottom: 20px;
  color: #4a4a4a;
}

.form {
  display: flex;
  flex-direction: column;
}

.input-group {
  margin-bottom: 15px;
  text-align: left;
}

.input-group label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

.input-group input {
  width: 100%;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
  transition: border-color 0.3s;
}

.input-group input:focus {
  border-color: #ff6f61;
  outline: none;
}

.submit-button {
  padding: 10px;
  border: none;
  border-radius: 5px;
  background-color: #ff6f61;
  color: white;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.submit-button:hover {
  background-color: #e65c51;
}

.error-message {
  color: #ff4d4d;
  margin-top: 10px;
}

.success-message {
  color: #4caf50;
  margin-top: 10px;
}
</style>
